
  
  import utils from '../../utils'
  let todayStr = utils.getCurrentTime()
  import { mapGetters, mapActions } from 'vuex'
  import XButton from 'vux/src/components/x-button/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

  import None from '../../components/None'
  import TabelView from '../../components/TabelView'
  export default {
    components: {
      XButton,
      Scroller,
      Spinner,
      None,
      TabelView,
      Divider
    },
    data () {
      return {
        params: {
          ticketDate: todayStr,
          pageid: 1
        },
        shifts: [],
        status: {
          pulldownStatus: 'default',
          pullupStatus: 'default'
        },
        downConf: { // 下拉配置
          content: 'Pull Down To Refresh',
          height: 60,
          autoRefresh: false,
          downContent: '下拉刷新',
          upContent: '加载中...',
          loadingContent: '加载中...',
          clsPrefix: 'xs-plugin-pulldown-'
        },
        upConf: {
		  content: 'Pull Up To Refresh',
		  pullUpHeight: 60,
		  height: 40,
		  autoRefresh: false,
		  downContent: '上拉加载',
		  upContent: '加载中...',
		  loadingContent: '加载中...',
		  clsPrefix: 'xs-plugin-pullup-'
		}
      }
    },
    computed: {
      ...mapGetters({
        ChkShifts: 'QueryChkShifts'
      }),
    },
    methods: {
      ...mapActions([
        'QueryChkShifts'
      ]),
      changeShift (index,event) {
      	if(event.clientX > 0) {
	    	const $this = this
	    	plus.nativeUI.confirm("是否设置为验票班次", (e) => {
	    	  if(e.index == 0) {
	    	    const shift = $this.shifts[index]
	            $this.$store.commit('QueryRecenShifts', { res: shift })
	            $this.$router.go(-1)
	    	  }
	    	})
    	}
      }
    },
    watch: { // 监听下拉下拉的状态
      status (e) {
        const $this = this
        if (e.pulldownStatus === 'loading') {  //下拉刷新
          setTimeout(() => {
            $this.QueryChkShifts().then((res) => {
              $this.$nextTick(() => {
		        this.$refs.scroller.reset()
		      })
              $this.status.pulldownStatus = 'default'
            })
          }, 500)
        } else if (e.pullupStatus === 'loading') { //上拉加载
          setTimeout(() => {
            this.params.pageid++
            $this.QueryChkShifts(this.params).then((res) => {
              $this.shifts = this.shifts.concat(this.ChkShifts)
              $this.$nextTick(() => {
		        $this.$refs.scroller.reset()
		      })
              if($this.ChkShifts.length < 15) {
          	    $this.status.pullupStatus = 'disabled'
              }else {
                $this.status.pullupStatus = 'default'
              }
            })
          }, 500)
        }
      }
    },
    created () {
      this.$store.state.responseData['QueryChkShifts'] = []
      this.$store.state.comm.loadMore = 'loading'
      this.QueryChkShifts(this.params).then((res) => {
      	  this.$store.state.comm.loadMore = 'success'
          this.shifts = this.ChkShifts
          if(this.shifts.length < 15) {
          	this.status.pullupStatus = 'disabled'
          }
	      this.$nextTick(() => {
	        this.$refs.scroller.reset()
	      })
	  })
    }
  }



